import React, { Component } from 'react';
import './about-us.component.css';

import aboutUsBanner from '../../assets/gamer-profiles/about-us-banner.png';
import gamerProfiles from '../../assets/gamer-profiles/gamer-profiles.png';
import alexProfile from '../../assets/gamer-profiles/Alex_tag.png';
import jasmineProfile from '../../assets/gamer-profiles/Jasmine_tag.png';
import meganProfile from '../../assets/gamer-profiles/Megan_tag.png';
import zachProfile from '../../assets/gamer-profiles/Zach_tag.png';

export default class AboutUs extends Component {

    render() {

        return(
            <body className="aboutBody">
                <div id="about-us" className="container">

                    <img id="aboutUsBanner" src={aboutUsBanner} alt="About Us banner graphic" />

                    <img id="gamerProfiles" src={gamerProfiles} alt="Gamer profiles graphic" />

                    <div id="profiles">
                        <img id="alexProfile" class="profile" src={alexProfile} alt="Gamer profile for Alex" />
                        <img id="jasmineProfile" class="profile" src={jasmineProfile} alt="Gamer profile for Jasmine" />
                        <img id="meganProfile"  class="profile" src={meganProfile} alt="Gamer profile for Megan" />
                        <img id="zachProfile"  class="profile" src={zachProfile} alt="Gamer profile for Zach" />
                    </div>

                </div>
            </body>
        );

    }

}