import React, { Component } from 'react';
import './home.component.css';

import headerGraphic from '../../assets/graphics/header-HP.png';
import missionGraphic from '../../assets/graphics/mission-HP.png';
import historyGraphic from '../../assets/graphics/history-HP.png';
import beliefsGraphic from '../../assets/graphics/beliefs-HP.png';
import testimonialsGraphic from '../../assets/graphics/testimonials-HP.png';
import moonHeaderGraphic from '../../assets/graphics/moon-header-HP.png';
import moonGraphic from '../../assets/graphics/moon-HP.png';
import planetGraphic from '../../assets/graphics/planet-HP.png';

export default class Home extends Component {

    constructor(props) {
        super(props);

        this.onSubmit = this.onSubmit.bind(this);

    }

    onSubmit() {

    }

    render() {

        return(

            <div id="home" className="container">

                <div className="newsletter">
                    <p>Submit name &amp; email for Work. Study. Play! Newsletters.</p>

                    <form onSubmit={this.onSubmit}>
                        <label>Name
                            <input type="text" className="form-control newsletter-custom form-control-lg" />
                        </label>
                        <br />
                        <label>Email
                            <input type="text" className="form-control newsletter-custom form-control-lg" />
                        </label>
                        <input type="submit" className="form-control newsletter-custom form-control-sm"
                                    aria-label="Submit" aria-describedby="basic-addon2" />
                    </form>
                </div>

                <div className="container">

                    <div id="header-HP" className="container">

                        <div id="newsletter-HP" className="container">
                            <img id="headerGraphic" className="graphics" src={headerGraphic} alt="Header Graphic" />

                            <form id="newsletter" className="container" onSubmit={this.onSubmit}>
                                <input id="newsletter-email" type="text" className="form-control newsletter-custom form-control-lg" />
                                <input id="newsletter-submit" type="submit" className="form-control newsletter-custom form-control-sm"
                                            aria-label="Submit" aria-describedby="basic-addon2" />
                            </form>
                        </div>

                        <img id="moonHeaderGraphic" className="graphics" src={moonHeaderGraphic} alt="Moon Graphic" />

                    </div>

                    <div id="mission" className="container">

                        <img id="planetGraphic" className="graphics" src={planetGraphic} alt="Planet graphic with spaceship flying around." />
                        <img id="missionGraphic" className="graphics" src={missionGraphic} alt="Mission Graphic" />

                    </div>

                    <div id="history">
                        
                        <img id="histroyGraphic" className="graphics" src={historyGraphic} alt="History Graphic" />

                    </div>

                    <div id="beliefs">
                        
                        <img id="beliefsGraphic" className="graphics" src={beliefsGraphic} alt="Beliefs Graphic" />

                    </div>

                    <img id="moonGraphic" className="graphics" src={moonGraphic} alt="Small moon graphic with spaceship and planets around." />

                    <div id="testimonials">
                        
                        <img id="testimonialsGraphic" className="graphics" src={testimonialsGraphic} alt="Testimonials Graphic" />

                    </div>
                </div>

                <div id="socialMediaIcons">
                    <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css" />
                    <a href="linkedin.com" className="fa fa-linkedin"> </a>
                    <a href="twitter.com" className="fa fa-twitter"> </a>
                    <a href="instagram.com" className="fa fa-instagram"> </a>
                </div>
                
            </div>

        );

    }

}

