import React, { Component } from 'react';
import './contact.component.css';

export default class Contact extends Component {

    constructor(props) {
        super(props);

        this.onSubmit = this.onSubmit.bind(this);
    }

    onSubmit() {

    }

    render() {

        return(

            <div id="contact" className="container">

                <h1 id="contact-title">Contact</h1>

                <p>Don't hesitate to reach out with the contact information below, or send a message using the form.</p>

                <div id="contact-types" className="container">

                    <div className="flex-1">
                        <h2>Get in Touch</h2>

                        <a href="mailto:info@workstudyplay.fun">info@workstudyplay.fun</a>

                        <br /><br />

                        <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css" />
                        <span className="fa fa-linkedin" />
                        <span className="fa fa-twitter" />
                    </div>

                    <div className="flex-2">
                        <h2>Send Us a Message</h2>

                        <form onSubmit={this.onSubmit}>
                            <input type="text" className="form-control contact-custom form-control-sm" placeholder="Name"
                                aria-label="Name" aria-describedby="basic-addon2" />
                            <input type="text" className="form-control contact-custom form-control-sm" placeholder="Email"
                                aria-label="Email" aria-describedby="basic-addon2" />
                            <textarea className="form-control contact-custom form-control-sm" placeholder="Message"
                                aria-label="Message" aria-describedby="basic-addon2" rows="5" />
                            <input type="submit" className="form-control contact-custom form-control-sm"
                                aria-label="Submit" aria-describedby="basic-addon2" />
                        </form>
                    </div>

                </div>

            </div>

        );

    }

}