import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './header.component.css';
import logo from "../../wsp-logo.png"

var opened = false;

export default class Header extends Component {

    constructor() {
        super();

        this.openMenu = this.openMenu.bind(this);
    }

    openMenu(e) {
        if (opened) {
            opened = false;
            e.target.classList.toggle("changeBack");
            document.getElementById('mobile-menu').style.display = "none";
        }
        else {
            opened = true;
            e.target.classList.toggle("change");
            document.getElementById('mobile-menu').style.display = "block";
        }
    }

    render() {

        return(

            <div id="header" className="header">

                <nav className="navbar navbar-expand-lg bg-light navbar-light navbar-custom h-collapse">

                    <div className="container">

                        <Link to="/home" className="navbar-brand">
                            <img src={logo} id="wsp-logo" alt="WSP Logo" />
                            Work. Study. Play!
                        </Link>

                            <ul className="nav ml-auto ul-custom mb">
                                <li className="nav-item active li-custom">
                                    <Link to="/home" className="nav-link">Home</Link>
                                </li>
                                <li className="nav-item active li-custom">
                                    <Link to="/about-us" className="nav-link">About Us</Link>
                                </li>
                                <li className="nav-item active li-custom">
                                    <Link to="/contact" className="nav-link">Contact</Link>
                                </li>
                            </ul>
                
                            <div id="menu-icon" onClick={this.openMenu}>
                                <div id="bar1" className="bar1"></div>
                                <div id="bar2" className="bar2"></div>
                                <div id="bar3" className="bar3"></div>
                            </div>

                            <div id="mobile-menu">
                                <ul className="nav mr-auto ul-custom-m">
                                    <li className="nav-item active li-custom-m">
                                        <Link to="/home" className="nav-link">Home</Link>
                                    </li>
                                    <li className="nav-item active li-custom-m">
                                        <Link to="/about-us" className="nav-link">About Us</Link>
                                    </li>
                                    <li className="nav-item active li-custom-m">
                                        <Link to="/contact" className="nav-link">Contact</Link>
                                    </li>
                                </ul>
                            </div>
                            
                    </div>

                </nav>

            </div>

        );

    }

}